  width: 80%;
}

.suportContainer {
  position: fixed;
  width: 70px;
  height: 70px;
  background-color: rgba(255, 255, 255, 0);
  right: 50px;
  bottom: 30px;
  border-radius: 50%;
  border: 4px solid white;
  display: flex;
  align-items: center;
