.site-footer {
  background-color: #00114d;
  padding: 45px 0 20px;
  font-size: 15px;
  line-height: 24px;
  color: #dddddd;
}
.site-footer hr {
  border-top-color: #bbb;
  opacity: 0.5;
}
.site-footer hr.small {
  margin: 20px 0;
}
.site-footer h6 {
  color: #dddddd;
  font-size: 16px;
  text-transform: uppercase;
  margin-top: 5px;
  letter-spacing: 2px;
}
.site-footer a {
  color: #dddddd;
  transition: 0.3s;
}
.site-footer a:hover {
  color: #00adc4;
  text-decoration: none;
}
.footer-links {
  padding-left: 0;
  list-style: none;
}
.footer-links li {
  display: block;
}
.footer-links a {
  color: #dddddd;
}
.footer-links a:active,
.footer-links a:focus,
.footer-links a:hover {
  color: #07b7ce;
  text-decoration: none;
}
.footer-links.inline li {
  display: inline-block;
}
.site-footer .social-icons {
  text-align: right;
}
.site-footer .social-icons a {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 40px;
  height: 40px;
  line-height: 40px;
  margin-left: 6px;
  margin-right: 0;
  border-radius: 100%;
  background-color: #fdfdfd00;
  border: 2px solid white;
}
.copyright-text {
  margin: 0;
}
@media (max-width: 991px) {
  .site-footer [class^='col-'] {
    margin-bottom: 30px;
  }
}
@media (max-width: 767px) {
  .site-footer {
    padding-bottom: 0;
  }
  .site-footer .copyright-text,
  .site-footer .social-icons {
    text-align: center;
  }
}
.social-icons {
  padding-left: 0;
  margin-bottom: 0;
  list-style: none;
}
.social-icons li {
  display: inline-block;
  margin-bottom: 4px;
}
.social-icons li.title {
  margin-right: 15px;
  text-transform: uppercase;
  color: #96a2b2;
  font-weight: 700;
  font-size: 13px;
}
.social-icons a {
  background-color: #eceeef;
  color: #818a91;
  font-size: 16px;
  display: inline-block;
  line-height: 44px;
  width: 44px;
  height: 44px;
  text-align: center;
  margin-right: 8px;
  border-radius: 100%;
  transition: all 0.2s linear;
}
.social-icons a:active,
.social-icons a:focus,
.social-icons a:hover {
  color: #dddddd;
  background-color: #29a9fe00;
}
.social-icons.size-sm a {
  line-height: 34px;
  height: 34px;
  width: 34px;
  font-size: 14px;
}
.social-icons a.linkedin:hover {
  background-color: #0441c4;
}
.social-icons a.github:hover {
  background-color: #000000;
}
.social-icons a.instagram:hover {
  background: rgb(161, 34, 195);
  background: linear-gradient(
    214deg,
    rgba(161, 34, 195, 1) 0%,
    rgba(181, 34, 168, 1) 39%,
    rgba(195, 34, 151, 1) 50%,
    rgba(253, 188, 45, 1) 100%
  );
}
.social-icons a.dribbble:hover {
  background-color: #ea4c89;
}
@media (max-width: 767px) {
  .social-icons li.title {
    display: block;
    margin-right: 0;
    font-weight: 600;
  }
}

.loginBackground {
  width: 100vw;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  background-image: url(/Pc_Express_2.0/static/media/wallpeaper.745d9597.jpg);
  background-size: cover;
}

.loginBackground p {
  font: 700 1.2rem Roboto;
  position: absolute;
  top: 100px;
  color: white;
}
.loginBackground h5 {
  font: 400 1rem 'Roboto';
  position: absolute;
  top: 140px;
  color: white;
}

.loginInputs {
  display: flex;
  flex-direction: column;
  padding-top: 200px;
}
.showPassword {
  position: absolute;
  right: 10px;
  cursor: pointer;
  transition: 0.8s;
}
.showPassword:hover {
  opacity: 70%;
}

.passwordContainer {
  position: relative;
}
.loginInputs input[type='password'],
.loginInputs input[type='text'],
.loginInputs input[type='email'] {
  width: 80vw;
  height: 45px;
  margin-bottom: 40px;
  border-bottom: 2px solid rgb(182, 182, 182);
  background-color: rgba(0, 0, 0, 0);
  border-top: none;
  border-left: none;
  border-right: none;
  outline: none;
  font: 700 1rem Roboto;
  color: rgb(233, 233, 233);
  padding-left: 20px;
  transition: 0.8s;
}

.loginInputs input[type='password']:focus,
.loginInputs input[type='text']:focus,
.loginInputs input[type='email']:focus {
  border: 2px solid white;
  border-top: none;
  border-left: none;
  border-right: none;
}

.loginInputs input[type='password']::-webkit-input-placeholder, .loginInputs input[type='email']::-webkit-input-placeholder, .loginInputs input[type='text']::-webkit-input-placeholder {
  color: white;
  -webkit-transition: 0.8s;
  transition: 0.8s;
}

.loginInputs input[type='password']:-ms-input-placeholder, .loginInputs input[type='email']:-ms-input-placeholder, .loginInputs input[type='text']:-ms-input-placeholder {
  color: white;
  -ms-transition: 0.8s;
  transition: 0.8s;
}

.loginInputs input[type='password']::-ms-input-placeholder, .loginInputs input[type='email']::-ms-input-placeholder, .loginInputs input[type='text']::-ms-input-placeholder {
  color: white;
  -ms-transition: 0.8s;
  transition: 0.8s;
}

.loginInputs input[type='password']::placeholder,
.loginInputs input[type='email']::placeholder,
.loginInputs input[type='text']::placeholder {
  color: white;
  transition: 0.8s;
}

.loginInputs input[type='password']:focus::-webkit-input-placeholder, .loginInputs input[type='email']:focus::-webkit-input-placeholder, .loginInputs input[type='text']:focus::-webkit-input-placeholder {
  opacity: 40%;
}

.loginInputs input[type='password']:focus:-ms-input-placeholder, .loginInputs input[type='email']:focus:-ms-input-placeholder, .loginInputs input[type='text']:focus:-ms-input-placeholder {
  opacity: 40%;
}

.loginInputs input[type='password']:focus::-ms-input-placeholder, .loginInputs input[type='email']:focus::-ms-input-placeholder, .loginInputs input[type='text']:focus::-ms-input-placeholder {
  opacity: 40%;
}

.loginInputs input[type='password']:focus::placeholder,
.loginInputs input[type='email']:focus::placeholder,
.loginInputs input[type='text']:focus::placeholder {
  opacity: 40%;
}

.loginInputs input[type='submit'] {
  width: 80vw;
  height: 60px;
  border-radius: 18px;
  border: 2px solid white;
  background-color: rgba(255, 255, 255, 0);
  outline: none;
  color: white;
  cursor: pointer;
  font: 700 1rem Roboto;
  transition: 0.8s;
}

.loginInputs input[type='submit']:hover {
  background-color: white;
  color: black;
}

.loginLinks {
  color: white;
  height: 20vh;
  display: flex;
  flex-direction: row;
  margin-top: 20px;
  width: 90vw;
  border-top: none;
  display: flex;
  align-items: center;
  padding-top: 100px;
  justify-content: space-around;
}

.loginLinks a {
  font: 400 2rem Roboto;
  margin-top: 10px;
}

.loginLinks a:hover {
  opacity: 70%;
}

@media (min-width: 600px) {
  .loginLinks {
    width: 80vw;
  }
  .loginInputs input[type='password'],
  .loginInputs input[type='email'],
  .loginInputs input[type='text'],
  .loginInputs input[type='submit'] {
    width: 60vw;
  }
}

@media (min-width: 700px) {
  .loginLinks {
    width: 60vw;
  }
  .loginInputs input[type='password'],
  .loginInputs input[type='email'],
  .loginInputs input[type='text'],
  .loginInputs input[type='submit'] {
    width: 40vw;
  }
}

@media (min-width: 1000px) {
  .loginLinks {
    width: 40vw;
  }
  .loginInputs input[type='password'],
  .loginInputs input[type='email'],
  .loginInputs input[type='text'],
  .loginInputs input[type='submit'] {
    width: 40vw;
  }
}
@media (max-height: 700px) {
  .loginLinks {
    margin-bottom: 40px;
  }
}

